import { videosState } from './config';

export default function pagination(elements) {
  const elems = elements;
  const currPage = Math.ceil(videosState.rewoundVideos / videosState.numRewindVideos) + 1;

  elems.currPageBtn.textContent = currPage;
  elems.pageBtn1.textContent = currPage >= 3 ? currPage - 2 : 1;
  elems.pageBtn2.textContent = currPage >= 3 ? currPage - 1 : 1;
  elems.pageBtn3.textContent = currPage + 1;
  elems.pageBtn4.textContent = currPage + 2;
  elems.tooltip.textContent = `You are on the ${currPage} page`;
}
