import { apiKey, videosState } from './config';

function addVideoInDom(flag, elems) {
  const elements = elems;
  const videos = flag ? elems.videoDomElems : elems.videoDomElems.slice(-15);
  let resStr = '';

  videos.forEach((el) => {
    resStr += `
      <p id="video_title" class="video_title"></p>
      <li id="video" class="video">
        <div class="preview_container">
            <a href="https://www.youtube.com/watch?v=${el.id}" target="_blank" target="_blank" id="video_title" class="video_title">
              ${el.snippet.title}
            </a>
            <img
              id="video_preview"
              class="video_preview"
              src="${el.snippet.thumbnails.medium.url}"
              alt="video_preview_picture"
            />
        </div>
        <p id="channel_name" class="channel_name">${el.snippet.channelTitle}</p>
        <p id="publish_date" class="publish_date">
          ${el.snippet.publishedAt.slice(0, 10)}
        </p>
        <p id="views" class="views">${el.statistics.viewCount}</p>
        <p id="description" class="description">
          ${el.snippet.description}
        </p>
      </li>`;
  });
  if (flag) {
    elements.results.innerHTML = resStr;
  } else {
    elements.results.insertAdjacentHTML('beforeend', resStr);
  }
}

export default async function makeRequest(elems, isSearch, query, token) {
  const elements = elems;
  const result1 = await fetch(`https://www.googleapis.com/youtube/v3/search?key=${apiKey}&type=video&part=snippet&maxResults=15&q=${query}&pageToken=${token || ''}`);
  const data1 = await result1.json();

  let ids = '';
  videosState.prevPageToken = data1.prevPageToken;
  videosState.nextPageToken = data1.nextPageToken;

  data1.items.forEach((el) => {
    ids += `${el.id.videoId},`;
  });

  const result2 = await fetch(`https://www.googleapis.com/youtube/v3/videos?key=${apiKey}&id=${ids},&part=snippet,statistics`);
  const data2 = await result2.json();
  if (isSearch) {
    elements.buttons.style.display = 'flex';
    elements.videoDomElems = data2.items;
  } else {
    elements.videoDomElems.push(...data2.items);
  }

  addVideoInDom(isSearch, elements);
}
