import { videosState } from './config';
import pagination from './pagination';

export default function resizeWindow(elements) {
  const elems = elements;

  if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|BB|PlayBook|IEMobile|Windows Phone|Kindle|Silk|Opera Mini/i.test(navigator.userAgent)) {
    videosState.numRewindVideos = 1;
    elems.wrapperResults.style.width = '380px';
  } else if (window.innerWidth > 1480) {
    videosState.numRewindVideos = 4;
    elems.wrapperResults.style.width = '1520px';
  } else if (window.innerWidth < 1480 && window.innerWidth > 1100) {
    videosState.numRewindVideos = 3;
    elems.wrapperResults.style.width = '1140px';
  } else if (window.innerWidth < 1100 && window.innerWidth > 720) {
    videosState.numRewindVideos = 2;
    elems.wrapperResults.style.width = '760px';
  } else if (window.innerWidth < 720) {
    videosState.numRewindVideos = 1;
    elems.wrapperResults.style.width = '380px';
  }
  pagination(elems);
}
