export function showTooltip(elements) {
  const { tooltip } = elements;
  tooltip.style.opacity = 1;
  tooltip.style.transform = 'translate3d(0,0,0)';
}

export function hideTooltip(elements) {
  const { tooltip } = elements;
  tooltip.style.opacity = 0;
  tooltip.style.transform = 'translate3d(0,-50px,0) rotate3d(1,1,1,-6deg)';
}
