import makeRequest from './makeRequest';
import { videosState } from './config';
import pagination from './pagination';

export function moveVideos(res, change, n, elements) {
  const results = res;
  const temp = 380 * n;
  let left = videosState.positionLeft;

  if (change < 0 && left === 0) {
    results.style.left = '0px';
  } else if (change < 0) {
    videosState.rewoundVideos -= n;
    videosState.totalLength -= temp;
    left += temp;
    results.style.left = `${left}px`;
  } else if (change > 0) {
    videosState.rewoundVideos += n;
    videosState.totalLength += temp;
    left -= temp;
    results.style.left = `${left}px`;
    if (videosState.totalLength >= 3040) {
      videosState.totalLength = 0;
      makeRequest(elements, false, elements.searchInput.value, videosState.nextPageToken);
    }
  }
  if (left > 0) {
    results.style.left = '0px';
    left = 0;
    videosState.rewoundVideos = 0;
  }
  videosState.positionLeft = left;
  pagination(elements);
}

export function makeCarousel(elems) {
  const elements = elems;
  let isDown = false;
  let startingX1;
  let startingX2;
  let flag = false;

  elements.results.addEventListener('mousedown', (e) => {
    flag = true;
    elements.results.style.transition = 'left .1s, right .1s, transform .2s';
    elements.results.style.cursor = 'grabbing';
    isDown = true;
    elements.results.classList.add('active');
    startingX1 = e.pageX - elements.results.offsetLeft;
    startingX2 = e.pageX;
  });

  function mouseUpLeaveHandler(e) {
    elements.results.style.cursor = 'grab';
    isDown = false;
    elements.results.classList.remove('active');
    elements.results.style.transition = 'left .3s, right .3s, transform .2s';
    const change = startingX2 - e.pageX;

    moveVideos(elements.results, change, videosState.numRewindVideos, elements);
  }

  elements.results.addEventListener('mouseleave', (e) => {
    if (flag) {
      flag = false;
      mouseUpLeaveHandler(e);
    }
  });
  elements.results.addEventListener('mouseup', (e) => {
    flag = false;
    mouseUpLeaveHandler(e);
  });

  elements.results.addEventListener('mousemove', (e) => {
    if (!isDown) return;
    e.preventDefault();
    const change = e.pageX - startingX1;
    elements.results.style.left = `${change}px`;
  });

  elements.results.addEventListener('touchstart', (e) => {
    flag = true;
    elements.results.style.transition = 'left .1s, right .1s, transform .2s';
    isDown = true;
    elements.results.classList.add('active');
    startingX1 = e.touches[0].clientX - elements.results.offsetLeft;
    startingX2 = e.touches[0].clientX;
  });

  elements.results.addEventListener('touchmove', (e) => {
    if (!isDown) return;
    e.preventDefault();
    const change = e.touches[0].clientX - startingX1;
    elements.results.style.left = `${change}px`;
  });

  elements.results.addEventListener('touchend', (e) => {
    if (flag) {
      flag = false;
      isDown = false;
      elements.results.classList.remove('active');
      elements.results.style.transition = 'left .3s, right .3s, transform .2s';
      const change = startingX2 - e.changedTouches[0].clientX;

      moveVideos(elements.results, change, videosState.numRewindVideos, elements);
    }
  });
}
