import '../css/style.css';
import getElements from './base';
import { makeCarousel, moveVideos } from './makeCarousel';
import resizeWindow from './resizeWindow';
import makeRequest from './makeRequest';
import { html, videosState } from './config';
import { showTooltip, hideTooltip } from './tooltipHandlers';
import pagination from './pagination';

export default function init() {
  document.body.innerHTML = html;
  const elements = getElements();
  let val;
  elements.results.style.transition = 'left .3s, right .3s, transform .2s';

  window.addEventListener('resize', () => {
    resizeWindow(elements);
  }, false);

  elements.searchForm.addEventListener('submit', (e) => {
    e.preventDefault();
    videosState.totalLength = 0;
    videosState.positionLeft = 0;
    videosState.rewoundVideos = 0;
    elements.results.style.left = '0';
    val = elements.searchInput.value;
    if (!val) return;
    makeRequest(elements, true, val);
    pagination(elements);
  });

  elements.buttons.addEventListener('click', (e) => {
    if (e.target.id === 'page2' || e.target.id === 'previous') {
      moveVideos(
        elements.results,
        -1, videosState.numRewindVideos,
        elements,
      );
    } else if (e.target.id === 'page3' || e.target.id === 'next') {
      moveVideos(
        elements.results,
        1, videosState.numRewindVideos,
        elements,
      );
    } else if (e.target.id === 'page1' || e.target.id === 'page4') {
      moveVideos(
        elements.results,
        e.target.id === 'page1' ? -1 : 1,
        (videosState.numRewindVideos * 2),
        elements,
      );
    }
  });

  elements.buttons.addEventListener('mousedown', () => showTooltip(elements));
  elements.buttons.addEventListener('mouseup', () => hideTooltip(elements));

  makeCarousel(elements);
  resizeWindow(elements);
}

document.addEventListener('DOMContentLoaded', init, false);
